      //     console.log(postObject);
      //    dispatch(createPost(postObject));
      var updates = {};
      updates[
        "/usersCraft/" + userEmail + "/posts/" + idPost
      ] = postObject;
      updates['/openmedia/' + idPost] = postObject;
      updates["/usersTemplates/projects/" + userEmail + '/' + idPost] = postObject;

      return firebase
        .database()
