function RecordsList({ records }) {
  const { register, handleSubmit, reset, watch } = useForm();
  const [filter, setFilter] = useState({ d: null, k: null });

  const onSubmit = (data) => {
    console.log(data);
    setFilter({ d: data.d, k: data.k });
  };

  let uniqueD = [...new Set(records.map((item) => item.d))];
  let uniqueK = [...new Set(records.map((item) => item.k))];

  // console.log(uniqueD); console.log(uniqueK); console.log(filter);

  let filteredDrecords = !!filter.d
    ? records.filter((item) => item.d === filter.d)
    : records.map((item) => item);
  let filteredKrecords = !!filter.k
    ? filteredDrecords.filter((item) => item.k === filter.k)
    : filteredDrecords.map((item) => item);

  return (
    <Container>
      <form onSubmit={handleSubmit(onSubmit)}>
        <Form.Row>
          <Form.Group as={Col} controlId="formAssetIncreaseLiabiliesDecrease">
            <Form.Label> +,  -</Form.Label>
            <Form.Control as="select" name="d" ref={register} required>
              {uniqueD.map((item) => (
                <option key={item}>{item}</option>
              ))}
            </Form.Control>
          </Form.Group>
          <Form.Group as={Col} controlId="formAssetDecreaseLiabiliesIncrease">
            <Form.Label> -,  +</Form.Label>
            <Form.Control as="select" name="k" ref={register} required>
              {uniqueK.map((item) => (
                <option key={item}>{item}</option>
              ))}
            </Form.Control>
          </Form.Group>
        </Form.Row>
        <Container>
          <Row>
            <Col>
              <Button variant="outline-secondary" type="submit">
                 
              </Button>
            </Col>
            <Col>
              <Button
                variant="outline-secondary"
                onClick={() => setFilter({ d: null, k: null })}
              >
                 
              </Button>
            </Col>
          </Row>
        </Container>
      </form>

      {filteredKrecords.map((row, index) => (
        <Row key={index}>
          <Col xs={2}>
            <small>{row.period}</small>
          </Col>
          <Col xs={3}>
            <small>{row.d}</small>
          </Col>
          <Col xs={3}>
            <small>{row.k}</small>
          </Col>
          <Col xs={4}>
            <small>{row.sum}</small>
            {!!row?.type ? <small>{" " + row.type}</small> : <small> </small>}
          </Col>
        </Row>
      ))}
    </Container>
  );
}

